export type CommunityCandidate = { name: string; topic: string };

/**
 * 
 * AI 
 */
export function pickCommunityCandidate(trigger: LifestyleTrigger): CommunityCandidate | null {
  switch (trigger.category) {
    case 'education':
      return { name: '', topic: '' };
    case 'commute':
      return { name: '', topic: '5' };
    case 'noise':
      return { name: '', topic: '' };
    case 'pet':
      return { name: '', topic: '' };
    case 'life-change':
      return { name: '', topic: '' };
    case 'rental':
      return { name: '', topic: '' };
    case 'stress':
      return { name: '', topic: '' };
    case 'quality':
      return { name: '', topic: '' };
    case 'amenity':
      return { name: '', topic: '' };
    case 'heartbreak':
      return { name: '', topic: '' };
    case 'work-stress':
      return { name: '', topic: '' };
    case 'healing':
      return { name: '', topic: '' };
    case 'independence':
      return { name: '', topic: '' };
    default:
      return null;
  }
}

/**
 *  System Prompt
 */
export function buildEnhancedPrompt(
  triggers: LifestyleTrigger[],
  conversationRounds: number,
  messageStyle: 'brief' | 'expressive' | 'neutral',
  intent: ConversationIntent = 'just_chat',
  recentlyRecommended: boolean = false
): string {
  let prompt = MAIMAI_SYSTEM_PROMPT;
  
  // ============================================
  // 
  // ============================================
  if (intent === 'just_chat') {
    prompt += `\n\n

`;
  } else if (intent === 'explore_community') {
    prompt += `\n\n
 1 
 1 `;
  } else if (intent === 'real_estate') {
    prompt += `\n\n

 1-2 `;
  }
  
  // ============================================
  // 
  // ============================================
  prompt += `\n\n

1. (30%)
   
2. (70%)

`;
  
  // ============================================
  //  real_estate 
  // ============================================
  if (intent === 'real_estate') {
    prompt += `\n\n

 1-2 `;
  } else if (conversationRounds < 5) {
    prompt += `\n\n
 5 
`;
  } else if (conversationRounds < 8) {
    prompt += `\n\n

`;
  } else {
    prompt += `\n\n
`;
  }
  
  // ============================================
  // 
  // ============================================
  const mainTrigger = triggers[0];
  if (mainTrigger && intent !== 'just_chat') {
    const candidate = pickCommunityCandidate(mainTrigger);
    
    prompt += `\n\n
${mainTrigger.category}
${mainTrigger.sampleBridge}

  1 `;
    
    // 
    if (candidate && !recentlyRecommended) {
      prompt += `\n\n

${candidate.name}
${candidate.topic}
[[:${candidate.name}:${candidate.topic}]]

  1 `;
    }
  }
  
  // ============================================
  // 
  // ============================================
  if (recentlyRecommended) {
    prompt += `\n\n
 3 
`;
  }
  
  // ============================================
  // 
  // ============================================
  const styleHint = {
    brief: '',
    expressive: '',
    neutral: ''
  }[messageStyle];
  
  if (styleHint) {
    prompt += `\n\n${styleHint}`;
  }
  
  return prompt;
}
